:root {
  color-scheme: dark;
  --bg-1: #04060f;
  --bg-2: #0c1024;
  --card: rgba(255, 255, 255, 0.04);
  --card-strong: rgba(255, 255, 255, 0.08);
  --border: rgba(255, 255, 255, 0.08);
  --text: #e9edf5;
  --muted: #9aa7c4;
  --accent: #ff5f6d;
  --accent-2: #5cf4da;
  --shadow-soft: 0 16px 60px rgba(5, 8, 20, 0.45);
  --glow: 0 10px 40px rgba(92, 244, 218, 0.25);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Space Grotesk", "Roboto", system-ui, -apple-system, sans-serif;
  background: radial-gradient(circle at 20% 20%, rgba(92, 244, 218, 0.06), transparent 30%),
    radial-gradient(circle at 70% 0%, rgba(255, 95, 109, 0.08), transparent 35%),
    linear-gradient(135deg, var(--bg-1), var(--bg-2) 40%, #05060f);
  color: var(--text);
  min-height: 100vh;
}

body::after {
  content: "";
  position: fixed;
  inset: 0;
  pointer-events: none;
  background: radial-gradient(circle at 80% 60%, rgba(92, 244, 218, 0.06), transparent 32%),
    radial-gradient(circle at 10% 80%, rgba(255, 95, 109, 0.08), transparent 34%);
  filter: blur(40px);
  opacity: 0.9;
  z-index: -1;
}

a {
  color: inherit;
  text-decoration: none;
}

main.app-main {
  width: min(1200px, 94vw);
  margin: 0 auto;
  padding: 120px 0 80px;
}

h1,
h2,
h3,
h4 {
  margin: 0;
  line-height: 1.2;
  letter-spacing: -0.01em;
}

p {
  margin: 0;
  color: var(--muted);
}

button {
  font-family: "Space Grotesk", "Roboto", system-ui, -apple-system, sans-serif;
}

.pill {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  border-radius: 999px;
  background: linear-gradient(120deg, rgba(255, 95, 109, 0.2), rgba(92, 244, 218, 0.16));
  border: 1px solid var(--border);
  color: var(--text);
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 0.05em;
}
