/* CoverPageStyle.css */

.cover {
  position: relative;
  display: grid;
  grid-template-columns: 1.05fr 0.95fr;
  gap: 32px;
  align-items: center;
  width: 100%;
  background: linear-gradient(130deg, rgba(255, 95, 109, 0.12), rgba(92, 244, 218, 0.08)),
    linear-gradient(160deg, rgba(255, 255, 255, 0.03), rgba(255, 255, 255, 0.0));
  border: 1px solid var(--border);
  border-radius: 30px;
  padding: 48px;
  overflow: hidden;
  box-shadow: var(--shadow-soft);
}

.cover.has-art {
  grid-template-columns: 1fr 0.9fr;
}

.cover::after {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(circle at 20% 20%, rgba(255, 95, 109, 0.2), transparent 32%),
    radial-gradient(circle at 90% 30%, rgba(92, 244, 218, 0.12), transparent 32%);
  opacity: 0.7;
  pointer-events: none;
}

.coverText {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.coverText h1 {
  font-size: clamp(32px, 5vw, 48px);
  font-weight: 700;
  color: #ffffff;
  letter-spacing: -0.01em;
}

.coverText p {
  font-size: 18px;
  color: var(--muted);
  max-width: 500px;
}

.coverText em {
  font-size: 16px;
  color: #fff;
  font-style: normal;
  opacity: 0.9;
}

.searchBar {
  display: flex;
  gap: 12px;
  align-items: center;
  background: rgba(255, 255, 255, 0.04);
  padding: 10px 12px;
  border-radius: 16px;
  border: 1px solid var(--border);
  box-shadow: var(--shadow-soft);
  width: min(520px, 100%);
  margin-top: 8px;
}

.searchBar input {
  flex: 1;
  background: rgba(255, 255, 255, 0.06);
  border: 1px solid transparent;
  color: #fff;
  border-radius: 12px;
  padding: 14px 16px;
  font-size: 16px;
  outline: none;
}

.searchBar input::placeholder {
  color: rgba(255, 255, 255, 0.65);
}

.searchBar button {
  background: linear-gradient(120deg, rgba(255, 95, 109, 0.95), rgba(92, 244, 218, 0.9));
  color: #05060f;
  border: none;
  border-radius: 12px;
  padding: 12px 18px;
  font-weight: 700;
  cursor: pointer;
  transition: transform 0.15s ease, filter 0.2s ease;
}

.searchBar button:hover {
  filter: brightness(1.05);
  transform: translateY(-1px);
}

.coverImage {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  min-height: 260px;
  border-radius: 24px;
  overflow: hidden;
  border: 1px solid var(--border);
  background: var(--card);
  box-shadow: var(--shadow-soft);
}

.coverGlow {
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, rgba(5, 6, 15, 0.2), rgba(5, 6, 15, 0.9));
  z-index: 1;
}

.coverImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  filter: saturate(1.1);
}

@media (max-width: 960px) {
  .cover {
    grid-template-columns: 1fr;
    padding: 36px;
  }

  .coverImage {
    order: -1;
    max-height: 320px;
  }
}

@media (max-width: 600px) {
  .cover {
    padding: 28px;
  }

  .searchBar {
    flex-direction: column;
    align-items: flex-start;
  }

  .searchBar button {
    width: 100%;
  }
}
