/* Login.css */

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  padding: 28px;
}

.login-container h1 {
  color: #fff;
  margin-bottom: 6px;
}

.login-subtitle {
  color: var(--muted);
  margin-bottom: 14px;
}

.login-form {
  display: flex;
  flex-direction: column;
  width: min(420px, 100%);
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 18px;
  padding: 28px;
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.35);
  gap: 14px;
}

.login-input {
  background: rgba(255, 255, 255, 0.06);
  border: 1px solid var(--border);
  border-radius: 12px;
  padding: 14px 16px;
  color: #fff;
  font-size: 16px;
  outline: none;
  transition: border-color 0.2s ease, transform 0.15s ease;
}

.login-input:focus {
  border-color: rgba(255, 95, 109, 0.8);
  transform: translateY(-1px);
}

.login-button {
  margin-top: 6px;
  padding: 14px 20px;
  font-size: 16px;
  font-weight: 700;
  background: linear-gradient(120deg, rgba(255, 95, 109, 0.95), rgba(92, 244, 218, 0.9));
  color: #05060f;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  box-shadow: var(--glow);
  transition: filter 0.2s ease, transform 0.15s ease;
}

.login-button:hover {
  filter: brightness(1.03);
  transform: translateY(-1px);
}

.error-text {
  color: #ff7b7b;
  margin-top: 6px;
  background: rgba(255, 95, 109, 0.1);
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid rgba(255, 95, 109, 0.35);
}
