/* EditAccount.css */

.page-wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 24px;
  gap: 20px;
}

.page-title {
  font-size: 2rem;
  font-weight: 700;
  color: #fff;
}

.card {
  background: var(--card);
  width: 100%;
  max-width: 680px;
  padding: 28px;
  border-radius: 16px;
  border: 1px solid var(--border);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.35);
}

.section {
  margin-bottom: 20px;
}

.sub-title {
  font-size: 1.2rem;
  margin: 10px 0;
  color: #fff;
}

.inline {
  display: flex;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
}

.label {
  display: block;
  margin-bottom: 6px;
  font-weight: 600;
  font-size: 0.95rem;
  color: var(--muted);
}

.text {
  font-size: 1rem;
  color: #fff;
}

.input {
  width: 100%;
  max-width: 100%;
  padding: 12px;
  margin-bottom: 10px;
  border-radius: 10px;
  border: 1px solid var(--border);
  font-size: 1rem;
  background: rgba(255, 255, 255, 0.05);
  color: #fff;
  outline: none;
}

.input:focus {
  border-color: rgba(255, 95, 109, 0.8);
}

.readonly-input {
  opacity: 0.6;
}

.edit-button,
.save-button,
.cancel-button {
  padding: 10px 20px;
  color: #05060f;
  border: none;
  border-radius: 10px;
  font-size: 0.95rem;
  cursor: pointer;
  transition: transform 0.15s ease, filter 0.2s ease;
  font-weight: 700;
  box-shadow: var(--glow);
}

.edit-button {
  background: linear-gradient(120deg, rgba(255, 95, 109, 0.95), rgba(92, 244, 218, 0.9));
}

.save-button,
.cancel-button {
  background: linear-gradient(120deg, rgba(92, 244, 218, 0.9), rgba(255, 95, 109, 0.9));
}

.edit-button:hover,
.save-button:hover,
.cancel-button:hover {
  transform: translateY(-1px);
  filter: brightness(1.03);
}

.password-section {
  margin-top: 10px;
}

.delete-button {
  padding: 12px 20px;
  background: #ff5f6d;
  color: #05060f;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 0.95rem;
  font-weight: 700;
  transition: transform 0.15s ease;
  box-shadow: var(--glow);
}

.delete-button:hover {
  transform: translateY(-1px);
}
