.MovieShowWrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 100%;
  min-height: 320px;
}

.loadingOverlay {
  position: absolute;
  inset: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(5, 6, 15, 0.88);
  border-radius: 20px;
  border: 1px solid var(--border);
  backdrop-filter: blur(10px);
  z-index: 5;
}

@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.loadingOverlay p {
  color: #ffffff;
  font-size: 1.4rem;
  margin-top: 14px;
  font-weight: 600;
  letter-spacing: 1px;
  animation: blink 1.5s infinite;
}

.mediaHeading {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.mediaHeading h1 {
  font-size: 24px;
  color: #fff;
  font-weight: 700;
}

.mediaCard {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 16px;
  width: 100%;
}

.media {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.35);
  transition: transform 0.2s ease, border-color 0.2s ease, box-shadow 0.2s ease;
  cursor: pointer;
  position: relative;
}

.media:hover {
  transform: translateY(-6px);
  border-color: rgba(255, 95, 109, 0.5);
  box-shadow: var(--glow);
}

.mediaImage {
  position: relative;
  width: 100%;
  height: 260px;
  overflow: hidden;
}

.mediaImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.mediaImage::after {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, transparent 50%, rgba(5, 6, 15, 0.85));
}

.mediaImage span {
  position: absolute;
  top: 12px;
  left: 12px;
  z-index: 2;
  height: 34px;
  min-width: 34px;
  padding: 0 10px;
  border-radius: 999px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: rgba(5, 6, 15, 0.7);
  border: 1px solid var(--border);
  color: #fff;
  font-weight: 700;
  box-shadow: var(--glow);
}

.mediaImage button {
  position: absolute;
  top: 12px;
  right: 12px;
  z-index: 3;
  border: 1px solid var(--border);
  background: rgba(5, 6, 15, 0.75);
  backdrop-filter: blur(6px);
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: grid;
  place-items: center;
  cursor: pointer;
  transition: transform 0.15s ease, border-color 0.15s ease;
}

.mediaImage button:hover {
  transform: scale(1.05);
  border-color: rgba(255, 95, 109, 0.5);
}

.mediaInfo {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  gap: 6px;
  padding: 14px 14px 18px;
}

.mediaInfo h4 {
  color: #fff;
  font-weight: 700;
  margin: 0;
  font-size: 16px;
}

.mediaInfo p {
  margin: 0;
  color: var(--muted);
  font-size: 14px;
}

.buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 14px;
  padding-top: 6px;
}

.buttons p {
  font-weight: 600;
  color: var(--muted);
}

.btnPrev,
.btnNext {
  border: 1px solid var(--border);
  outline: none;
  color: #fff;
  background: linear-gradient(120deg, rgba(255, 95, 109, 0.9), rgba(92, 244, 218, 0.9));
  font-size: 15px;
  border-radius: 12px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 10px 14px;
  cursor: pointer;
  box-shadow: var(--glow);
  transition: transform 0.2s ease;
}

.btnPrev:hover,
.btnNext:hover {
  transform: translateY(-2px);
}

.emptyState {
  width: 100%;
  padding: 28px;
  border: 1px dashed var(--border);
  border-radius: 16px;
  text-align: center;
  color: var(--muted);
  background: rgba(255, 255, 255, 0.02);
}

.favorites-wrapper {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.favorites-wrapper h2 {
  color: #fff;
  font-size: 24px;
  font-weight: 700;
}

.favorites-grid {
  margin-top: 6px;
}
  
  .btnPrev:active, .btnNext:active {
    transform: translateY(1px);
    box-shadow: none;
  }
  
