/* MovieDetails.css */

.mainContainer {
  position: relative;
  width: 100%;
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  background-size: cover;
  background-position: center;
  color: #fff;
  border: 1px solid var(--border);
  border-radius: 20px;
  overflow: hidden;
  box-shadow: var(--shadow-soft);
}

.overlay {
  background: linear-gradient(135deg, rgba(5, 6, 15, 0.92), rgba(5, 6, 15, 0.72));
  flex: 1;
  padding: 28px;
}

.contentWrapper {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 320px 1fr;
  gap: 32px;
  padding: 20px;
}

.posterSection {
  position: relative;
}

.poster {
  width: 100%;
  border-radius: 14px;
  box-shadow: var(--shadow-soft);
  border: 1px solid var(--border);
  background: rgba(255, 255, 255, 0.04);
}

.infoSection {
  min-width: 280px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.title {
  margin: 0;
  font-size: clamp(28px, 5vw, 34px);
  font-weight: 700;
}

.tagline {
  font-style: italic;
  font-weight: normal;
  color: #ccc;
  margin: 0 0 10px;
}

.rating {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 10px 14px;
  border-radius: 12px;
  width: fit-content;
  background: rgba(255, 255, 255, 0.06);
  border: 1px solid var(--border);
}

.star {
  color: #ffc107;
}

.releaseRuntime {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  color: var(--muted);
  font-weight: 600;
}

.genreList {
  margin-bottom: 6px;
}

.genreWrapper {
  margin-top: 6px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.genreBadge {
  padding: 6px 10px;
  background: rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  font-size: 0.9rem;
  border: 1px solid var(--border);
}

.overview {
  margin-bottom: 10px;
  line-height: 1.6;
  color: var(--muted);
}

.productionContainer {
  margin-top: 12px;
}

.companyList {
  margin-top: 10px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 16px;
}

.companyItem {
  text-align: center;
  padding: 10px;
  border: 1px solid var(--border);
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.04);
}

.companyLogo {
  width: 100%;
  max-height: 60px;
  object-fit: contain;
  margin-bottom: 6px;
  background-color: #fff;
  border-radius: 4px;
  padding: 4px;
}

.loadingText {
  text-align: center;
  margin-top: 40px;
  font-size: 1.5rem;
  color: #fff;
}

.errorText {
  text-align: center;
  margin-top: 40px;
  color: #ff7b7b;
  font-size: 1.5rem;
}

@media (max-width: 960px) {
  .contentWrapper {
    grid-template-columns: 1fr;
  }

  .posterSection {
    max-width: 300px;
  }
}
