.navBarWrapper {
  position: fixed;
  inset: 0 0 auto 0;
  z-index: 999;
}

.navToolbar {
  display: flex;
  align-items: center;
  gap: 20px;
}

.logo {
  color: #fff;
  font-size: 32px;
  letter-spacing: 0.5px;
  font-family: "Playfair Display", "Jaro", serif;
  font-weight: 600;
  padding: 10px 14px;
  border-radius: 12px;
  background: linear-gradient(120deg, rgba(255, 95, 109, 0.14), rgba(92, 244, 218, 0.16));
  border: 1px solid var(--border);
  box-shadow: var(--glow);
}

.navLinks {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
  justify-content: center;
}

.nav-link {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 10px 14px;
  border-radius: 999px;
  border: 1px solid transparent;
  color: var(--text);
  font-weight: 600;
  transition: all 0.25s ease;
  letter-spacing: 0.01em;
  background: transparent;
}

.nav-link:hover {
  background: var(--card);
  border-color: var(--border);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.35);
}

.nav-link.active {
  background: linear-gradient(120deg, rgba(255, 95, 109, 0.9), rgba(92, 244, 218, 0.85));
  color: #05060f;
  border-color: transparent;
  box-shadow: var(--glow);
}

.nav-dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--accent-2);
  opacity: 0;
  transform: scale(0.5);
  transition: all 0.2s ease;
}

.nav-link.active .nav-dot,
.nav-link:hover .nav-dot {
  opacity: 1;
  transform: scale(1);
}

.authButtons {
  display: flex;
  align-items: center;
  gap: 10px;
}

.authButton {
  background: linear-gradient(120deg, rgba(255, 95, 109, 0.9), rgba(92, 244, 218, 0.9));
  color: #05060f;
  border-radius: 12px;
  padding: 10px 18px;
  text-transform: none;
  font-weight: 700;
  border: 1px solid transparent;
  box-shadow: var(--glow);
}

.authButton:hover {
  filter: brightness(1.05);
}

.authButton.ghost {
  background: transparent;
  color: var(--text);
  border-color: var(--border);
  box-shadow: none;
}

.authButton.ghost:hover {
  background: var(--card-strong);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.userButton svg {
  font-size: 30px;
}
